import { useRoutes, Navigate } from 'react-router-dom';
import CssBaseline from '@mui/material/CssBaseline';
import { useState, useEffect } from 'react';
import { MatxTheme } from './components';
// ALL CONTEXTS
import { AuthProvider } from './contexts/JWTAuthContext';
import SettingsProvider from './contexts/SettingsContext';
// ROUTES
import routes from './routes';
// FAKE SERVER
import '../fake-db';
import { navigations } from './navigations';

const defaultPage = navigations[0].children[0].path;

export default function App() {
    const [startpageTo, setStartPageTo] = useState(defaultPage);
    // const content = useRoutes(routes);
    const updatedRoutes = routes.map((route) => {
        if (route.path === '/') {
            return { ...route, element: <Navigate to={startpageTo} /> };
        }
        return route;
    });
    const content = useRoutes(updatedRoutes);
    const getStartPage = () => {
        if (
            localStorage.getItem('startpage') !== null &&
            localStorage.getItem('startpage') !== ''
        ) {
            const myPage = localStorage.getItem('startpage');
            setStartPageTo(myPage);
        } else {
            setStartPageTo(defaultPage);
        }
    };

    useEffect(() => {
        getStartPage();
    }, [content]);
    return (
        <SettingsProvider>
            <AuthProvider>
                <MatxTheme>
                    <CssBaseline />
                    {content}
                </MatxTheme>
            </AuthProvider>
        </SettingsProvider>
    );
}
